/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

p {
  line-height: 1.4;
}

.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 5rem 0;
}
.header__logo {
  max-width: 20rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__texto {
  margin: 10rem 0;
}
.header__heading {
  color: #FFFFFF;
  font-weight: bold;
  font-size: 4.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    font-size: 6rem;
    text-align: left;
    max-width: 50%;
  }
}

.footer {
  background-color: #313c4e;
  padding: 5rem 0 1rem 0;
}
.footer__img {
  width: 20rem;
}
@media (min-width: 768px) {
  .footer__content {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__section {
  display: flex;
  flex-direction: column;
  text-align: left;
}
.footer__section h3 {
  margin: 0 0 5rem 0;
  text-align: left;
  color: #84d9bc;
}
.footer__section li {
  color: #FFFFFF;
  padding-bottom: 1.5rem;
}
.footer__copyright {
  text-align: center;
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 1.4rem;
  border-top: 2px solid #84d9bc;
  padding-top: 1rem;
  margin-top: 5rem;
}

.btn__primary {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #000;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.btn__primary:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .btn__primary {
    width: auto;
    display: inline-block;
  }
}

.create {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .create {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.create__heading {
  font-size: 3.6rem;
  margin-top: 5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .create__heading {
    margin-top: 0;
  }
}
.create__heading::after {
  content: "";
  background-color: #84d9bc;
  display: block;
  height: 1rem;
  width: calc(100% + 1rem);
  margin: -1.4rem 0 0 0;
}

.benefits {
  background-color: #84d9bc;
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .benefits__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.benefits__img {
  transition: transform 0.3s;
}
@media (min-width: 768px) {
  .benefits__img {
    width: 50rem;
  }
}
.benefits__img :hover {
  transform: rotate(8deg);
}
.benefits__heading {
  font-size: 3.6rem;
  margin-top: 5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .benefits__heading {
    margin-top: 0;
  }
}
.benefits__heading::after {
  content: "";
  background-color: #FFFFFF;
  display: block;
  height: 1rem;
  width: calc(100% + 1rem);
  margin: -1.4rem 0 0 0;
}

.estadistics {
  margin: 0 auto;
  padding: 8rem 0;
}
.estadistics__heading {
  font-size: 3.6rem;
  margin-top: 5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
}
@media (min-width: 768px) {
  .estadistics__heading {
    margin-top: 0;
  }
}
.estadistics__heading::after {
  content: "";
  background-color: #84d9bc;
  display: block;
  height: 1rem;
  width: calc(100% + 1rem);
  margin: -1.4rem 0 0 0;
}

.pricing {
  background-color: #313c4e;
  padding: 5rem 0;
}
.pricing__content p {
  color: #FFFFFF;
  font-size: 1.7rem;
}
.pricing__heading {
  font-size: 3.6rem;
  margin-top: 5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .pricing__heading {
    margin-top: 0;
  }
}
.pricing__heading::after {
  content: "";
  background-color: #84d9bc;
  display: block;
  height: 1rem;
  width: calc(100% + 1rem);
  margin: -1.4rem 0 0 0;
}
.pricing__grid {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .pricing__grid {
    flex-direction: row;
    flex-grow: 1;
    justify-content: center;
    align-items: center;
  }
}
.pricing__list {
  text-align: center;
  line-height: 2.2;
}

.pricing__card {
  background-color: #84d9bc;
  padding: 5rem;
  height: 60rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-size: 10rem;
  background-position: 50% 2rem;
}
@media (min-width: 768px) {
  .pricing__card {
    border-radius: 3rem;
  }
}
.pricing__card h3 {
  margin-top: 10rem;
}
.pricing__card--white {
  background-color: #FFFFFF;
  border-radius: 0;
  height: 50rem;
}
.pricing__card--1 {
  background-image: url(../../../build/img/icono_inicio.svg);
}
.pricing__card--2 {
  background-image: url(../../../build/img/icono_negocio.svg);
  order: -1;
  z-index: 100;
}
@media (min-width: 768px) {
  .pricing__card--2 {
    order: unset;
  }
}
.pricing__card--3 {
  background-image: url(../../../build/img/icono_pro.svg);
}
.pricing__btn {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
}
.pricing__btn:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .pricing__btn {
    width: auto;
    display: inline-block;
  }
}
.pricing__btn--border {
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #313c4e;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  border: 2px solid #FFFFFF;
}
.pricing__btn--border:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .pricing__btn--border {
    width: auto;
    display: inline-block;
  }
}
.pricing__price {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 1.8rem;
}

.pricing__card {
  transition: transform 0.3s;
}
.pricing__card:hover {
  transform: scale(1.1);
}

/*TEstimonios*/
.reviews {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .reviews {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
.reviews__graphic {
  width: 40rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .reviews__graphic {
    grid-column: 1/3;
  }
}
.reviews__heading {
  font-size: 3.6rem;
  margin-top: 5rem;
  display: inline-block;
  margin-top: 5rem;
  color: #313c4e;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .reviews__heading {
    margin-top: 0;
  }
}
.reviews__heading::after {
  content: "";
  background-color: #84d9bc;
  display: block;
  height: 1rem;
  width: calc(100% + 1rem);
  margin: -1.4rem 0 0 0;
}
.reviews__card {
  background-color: #84d9bc;
  border: 0.5rem solid #313c4e;
  border-radius: 1rem;
  margin-bottom: 5rem;
  padding: 3rem;
  position: relative;
}
.reviews__card:last-of-type {
  margin-bottom: 0;
}
.reviews__card blockquote {
  margin: 0;
  padding-left: 6rem;
}
@media (min-width: 768px) {
  .reviews__card {
    max-width: 70%;
  }
  .reviews__card:nth-child(2) {
    margin-left: 10%;
  }
  .reviews__card:nth-child(3) {
    margin-left: 20%;
  }
  .reviews__card:nth-child(4) {
    margin-left: 30%;
  }
}
.reviews__card::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url(../../../build/img/comilla.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  top: 0;
}
@media (min-width: 768px) {
  .reviews__text-box {
    grid-column: 3/6;
  }
}